.contactMeMain {
    display: grid;
    grid-template-columns: 1fr;
    width: 100%;
    justify-items: center;
}
.ResumeSectionContactInfo {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    justify-content: center;
    width: 50%;
}
.ResumeSectionContactInfoC {
    margin: 0% auto;
}
.ResumeSection {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
}
.profileInfo {
    width: 50%;
}
@media screen and (max-width: 358px) {
    .profileInfo {
        font-size: 80%;
    }
}